import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { Fab } from "@material-ui/core";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import * as Yup from 'yup'
import { Autocomplete } from "@material-ui/lab"
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';


import { Formik, Form, ErrorMessage } from 'formik'

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {
  Grid, TextField
} from '@material-ui/core';
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader

import Swal from 'sweetalert2'
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { columns, data, errorCols, errorOptions } from "./data_header_table";
import authService from "../../../utils/services/auth";



let departamentos = []
let usuario = null
let dpt = null
//let cursosDpt = []
export default function MuiDatatableExample() {


  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [isFormValid, setIsFormValid] = useState(false);
  const [open, setOpen] = React.useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [updateDom, setUpdateDom] = useState(false);
  const [carregando, setCarregando] = useState(true);
  const [cursos, setCursos] = useState([]);
  const [mostrarMensagem, setMostrarMensagem] = useState(false);




  useEffect(() => {
    usuario = authService.getCurrentUser();
    setTimeout(() => {
      setCarregando(false);
    }, 5000);
    usuario.authorities[0] === 'ADMIN' ? init() : usuarioLagado();
    init();
  }, [])

  async function init() {
    setCarregando(true)
    const [listaCurso, dptRequest] = await Promise.all([requesicoes().buscarCurso('?campos=id,nome,responsavel,dpt'), requesicoes().buscarDepartamentos()]);
    departamentos = dptRequest.data;
    if (usuario.authorities[0] === 'DPT') {
     const cursosDpt = listaCurso.data.filter(c => c.dpt.id === dpt.dpt.id).map((c, i) => ({ ...c, NO: i + 1, dpt: c.dpt.nome }));
      if (cursosDpt.length !== 0) {
        setCursos(cursosDpt)
      } else {
        setMostrarMensagem(true);
      }
    
    } else {
      const cursosComDepartamento = listaCurso.data.map((curso,i) => {
        const departamento = departamentos.find(dep => dep.id === curso.dpt.id);
        return {
          ...curso,NO: i + 1,
          dpt: departamento ? departamento.nome : ''
        };
      });
      setCursos(cursosComDepartamento)

    }

    setUpdateDom(!updateDom);
    setCarregando(false)
  }
  async function usuarioLagado() {
    setCarregando(true);
    const usuarioDpt = await requesicoes().getUsuarioLogado(usuario.sub, '?campos=id,numero')
    dpt = usuarioDpt.data
    setCarregando(false)
    setUpdateDom(!updateDom);
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {

    setOpen(false);
  };

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    isRowExpandable: (dataIndex, expandedRows) => {
      const { errors } = cursos[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            tableData={cursos[5]}
            options={errorOptions}
          />
        </div>
      );
    },
    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (
        <Fab onClick={handleClickOpen} variant="extended" size="medium"
          style={{
            position: 'fixed',
            bottom: '60px', zIndex: 500,
            right: '15px'
          }} color="primary">Adicionar</Fab>
      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(cursos[item]));
    }
  };

  const validationSchema = Yup.object().shape({
    nome: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
  })

  return (
    <div>
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : 
      cursos.length !== 0 ?
        <MuiThemeProvider theme={getMuiTheme()}>
          <MUIDataTable
            title={'Listagem de Cursos'}
            columns={columns(cursos)}
            data={cursos}
            options={options}
          />
        </MuiThemeProvider>:
         mostrarMensagem && (
          <Alert severity="warning">
          <AlertTitle>Informação</AlertTitle>
          Este departamento não possui cursos. Se for um engano por favor contacte o  <strong>Serviço de planificação</strong> sobre esta informação.
        </Alert>
        ) 

      }

      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Cadastrar Curso</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Preencha os dados do curso que pretende cadastrar
          </DialogContentText>
          <Formik initialValues={{}} validationSchema={validationSchema}

            onSubmit={(values, { setSubmitting }) => {
              async function save(values) {

                try {

                  await Promise.resolve(requesicoes().postCurso([values]));
                  //setOpen(true)
                  Swal.fire(
                    'Sucess!',
                    'Curso cadastrado com sucesso!',
                    'success'
                  )
                  init();

                } catch (error) {
                  Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Ocorreu um erro ao cadastrar Curso!!',
                    footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                  })

                }
              }
              save(values);
              setTimeout(() => {
                setSubmitting(false);
              }, 500);
            }}>
            {(props) => {
              const { isSubmitting, setFieldValue, values } = props;
              return (
                <Form>

                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      <Input name="nome"
                        label="Nome do Curso"
                        placeholder="Digite Aqui..."
                        helperText={<ErrorMessage name='nome' />}
                        error={props.errors.nome && props.touched.nome} />

                    </Grid>
                    <Grid item xs={12} sm={12} >
                      <Autocomplete onChange={(e, v) => {
                        setFieldValue('dpt', v);
                      }}
                        fullWidth name="dpt"
                        getOptionLabel={option => option.nome}
                        options={departamentos} style={{ minWidth: 200 }}
                        renderInput={(params) => <TextField variant="outlined" margin="dense" label="Departamento"
                          fullWidth {...params} placeholder="Seleccione uma opção" />}
                      />

                    </Grid>

                    <Grid item xs={12} sm={12}>
                      <Input name="responsavel"
                        label="Diretor do Curso"
                        placeholder="Digite Aqui..."
                        helperText={<ErrorMessage name='responsavel' />}
                        error={props.errors.responsavel && props.touched.responsavel} />

                    </Grid>

                  </Grid>
                  <Button onClick={handleClose}>Cancelar</Button>
                  <Button type='submit' onClick={handleClose}>Salvar</Button>

                </Form>)
            }}
          </Formik>
        </DialogContent>
        <DialogActions>
        </DialogActions>
      </Dialog>
    </div>

  );
}
